<template>

    <v-card
            v-if="projectInfomation ==null ||( projectInfomation['img'] == null || projectInfomation['projectName'] == null)"
            outlined
            class="mx-auto"
            style="width: 400px; height: 300px; justify-content: center"
            align="center"


    >
        <v-skeleton-loader
                ref="skeleton"
                type="card"
                class="mx-auto"
        >
        </v-skeleton-loader>


    </v-card>

    <v-card
            v-else
            outlined
            class="mx-auto"
            max-width="400"
            max-height="400"
    >
        <v-img
                :src="projectInfomation['img']"
                max-width="400"
                max-height="300"

        ></v-img>

        <v-list-item three-line>
            <v-list-item-content>
                <v-list-item-title class="headline mb-1">
                    {{ projectInfomation['projectName'] }}
                    <v-chip
                            v-if="projectInfomation['projectType'] == 'every'"
                            class="ma-2"
                            color="blue"
                            text-color="white"
                    >
                        Everyone
                    </v-chip>
                    <v-chip
                            v-if="projectInfomation['projectType'] != 'every' && projectInfomation['permissionCount'] > 0 "
                            class="ma-2"
                            color="blue"
                            text-color="white"
                    >
                        shared
                    </v-chip>
                </v-list-item-title>
                <v-list-item-subtitle>Author : {{ projectInfomation['projectAuthorEmail'] }}</v-list-item-subtitle>
                <v-list-item-subtitle>Modified Date : {{ projectInfomation['date'] }}</v-list-item-subtitle>
                <v-list-item-subtitle>Comment : {{projectInfomation['comment']}}</v-list-item-subtitle>
            </v-list-item-content>
        </v-list-item>
        <v-card-actions>
            <!--toUrl(author, projectId, projectVersion, type) -->
            <v-btn text @click.native="$EventBus.$emit('chatKey', projectInfomation['author'])"
                   :to="toUrl(
                               projectInfomation['projectAuthor'],
                               projectInfomation['projectType'],
                               projectInfomation['projectId'],
                               projectInfomation['projectVersion'],
                               )">
                Edit
            </v-btn>

            <v-btn
                    v-if="projectInfomation['projectType'] != 'every'"
                    color="red"
                    text
                    @click="$EventBus.$emit('deleteProject',projectInfomation)"
            >
                Delete
            </v-btn>
        </v-card-actions>
    </v-card>

</template>

<script>
    import firebase from 'firebase';

    export default {
        name: "ProjectCard",
        props: {
            projectId: String,
            projectType: String
        },
        data() {
            return {
                projectInfomation: {}
            }
        },
        created() {
            var me = this


            firebase.database()
                .ref('definitions/' + me.projectId + '/shareOptions')
                .once('value', function (snapshot) {

                }).then(function (shareOption) {
                firebase.database()
                    .ref('definitions/' + me.projectId + '/versionLists/' + shareOption.val()['lastVersionName'] + '/versionInfo')
                    .once('value', function (snapshot) {
                        if (snapshot.exists()) {
                            // console.log(me.projectType, shareOption.val()['permissions'])
                            var permissionCount = 0
                            if (me.projectType!='every' && me.projectType, shareOption.val()['permissions'] != undefined) {
                                permissionCount = Object.keys(me.projectType, shareOption.val()['permissions']).length
                            }
                            var obj = {
                                projectAuthor: shareOption.val()['author'],
                                projectAuthorEmail: shareOption.val()['authorEmail'],
                                saveUser: snapshot.val().saveUser,
                                saveUserEmail: snapshot.val().saveUserEmail,
                                saveUserName: snapshot.val().saveUserName,
                                projectId: me.projectId,
                                projectVersion: shareOption.val()['lastVersionName'],

                                date: snapshot.val().date,
                                projectName: snapshot.val().projectName,
                                img: snapshot.val().img,
                                comment: snapshot.val().comment,
                                projectType: me.projectType,
                                permissionCount: permissionCount,
                            }
                            // console.log( me.projectId ,obj)
                            me.projectInfomation = obj
                            // comment:""
                            // date:"2020년 4월 18일 11시 52분 23초"
                            // img:"data:image/png;base64,... (60010 total length)"
                            // projectAuthor:"1RtvBx30xSg6vhjoiKtikwJAleX2"
                            // projectAuthorEmail:"sooheon45@uengine.org"
                            // projectId:"2797a2ce2f5d998f16c082e930b32968"
                            // projectName:"local2"
                            // projectType:"mine"
                            // projectVersion:"-M5ABsmDwv0MVvuFSTw-"
                            // saveUser:"1RtvBx30xSg6vhjoiKtikwJAleX2"
                            // saveUserEmail:"sooheon45@uengine.org"
                            // saveUserName:"이수헌"

                        }
                    })
            })


        },
        computed: {},
        methods: {
            toUrl(author, projectType, projectId, projectVersion) {
                // /storming/:userUid/:projectType/:projectId/:projectVersion/:projectRead/:projectWrite
                return "storming/" + author + "/" + projectType + "/" + projectId + "/" + projectVersion
            },
        },
    }

</script>
